$border: 1px solid transparent;
$border-radius: 30px;
$background-color: var(--primary);
$color: var(--white);
$font-size: 20px;
$padding: 10px 20px 12px;
$box-shadow: none;
$gap: var(--gap-xs);

%main-btn,
.main-btn {
  @extend %btn-reset;
  @extend %flex-center;

  width: fit-content;
  min-width: 50px;
  min-height: 50px;
  font-size: $font-size;
  line-height: 1.1;
  padding: $padding;
  border: $border;
  border-radius: $border-radius;
  color: $color;
  background-color: $background-color;
  box-shadow: $box-shadow;
  gap: $gap;

  svg {
    fill: var(--white);
  }
}

.main-btn--outlined {
  border-color: $background-color;
  color: $background-color;
  background-color: transparent;

  svg {
    fill: var(--primary);
  }
}
